}
.page__body {
  margin: 0;
  background-color: #ffffff;
  color: #283645;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
}
.visually-hidden {
  position: absolute;
  margin: -1px;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  opacity: 0;
}
.page-header {
  width: 100%;
  margin: 0 auto;
  background: image-set(url("../img/arm-mobile.png") 1x, url("../img/arm-mobile@2x.png") 2x) no-repeat center 176px/320px 286px, #283645 image-set(url("../img/header-back-mobile.png") 1x, url("../img/header-back-mobile@2x.png") 2x) no-repeat center 0/320px 462px;
  color: #ffffff;
}
@media (min-width: 660px) {
  .page-header {
    background: image-set(url("../img/arm-tablet.png") 1x, url("../img/arm-tablet@2x.png") 2x) no-repeat center 184px/660px 545px, #283645 image-set(url("../img/header-back-tablet.png") 1x, url("../img/header-back-tablet@2x.png") 2x) no-repeat center 0/660px 729px;
    padding-bottom: 87px;
  }
}
@media (min-width: 1200px) {
  .page-header {
    background: image-set(url("../img/arm-desktop.png") 1x, url("../img/arm-desktop@2x.png") 2x) no-repeat center 208px/1050px 645px, #283645 image-set(url("../img/header-back-desktop.png") 1x, url("../img/header-back-desktop@2x.png") 2x) no-repeat center 0/1200px 853px;
    padding-bottom: 120px;
  }
}
.page-header--catalog {
  background: none, #283645 image-set(url("../img/header-back-mobile.png") 1x, url("../img/header-back-mobile@2x.png") 2x) no-repeat center 0/320px 462px;
}
@media (min-width: 660px) {
  .page-header--catalog {
    background: none, #283645 image-set(url("../img/header-back-tablet.png") 1x, url("../img/header-back-tablet@2x.png")) no-repeat center 0/660px 729px;
  }
}
@media (min-width: 1200px) {
  .page-header--catalog {
    background: none, #283645 image-set(url("../img/header-back-desktop.png") 1x, url("../img/header-back-desktop@2x.png") 2x) no-repeat center 0/1200px 853px;
  }
}
.page-header--form {
  background: none, #283645 image-set(url("../img/header-back-mobile.png") 1x, url("../img/header-back-mobile@2x.png") 2x) no-repeat center 0/320px 462px;
}
@media (min-width: 660px) {
  .page-header--form {
    background: none, #283645 image-set(url("../img/header-back-tablet.png") 1x, url("../img/header-back-tablet@2x.png")) no-repeat center 0/660px 729px;
