.page-header__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 21px 22px;
  margin-bottom: 20px;
  background-color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 660px) {
  .page-header__navigation {
    padding: 27px 20px 43px 20px;
    margin-bottom: 102px;
  }
}
@media (min-width: 1200px) {
