  color: #d22856;
}
.page-header__logo:active {
  color: rgba(255, 255, 255, 0.3);
}
.page-header__menu-toggler {
  position: relative;
  width: 50px;
  height: 24px;
  border: none;
  background-color: transparent;
}
@media (min-width: 1200px) {
  .page-header__menu-toggler {
    display: none;
  }
}
.page-header__menu-toggler::before {
  position: absolute;
  top: -2px;
  left: 0;
  content: "";
  width: 50px;
  height: 5px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.page-header__menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: none;
}
.page-header__menu--open {
  position: absolute;
  top: 70px;
  left: 0;
  display: block;
  width: 100%;
  border-top: 1px solid #000000;
  background-color: #283645;
}
@media (min-width: 660px) {
  .page-header__menu {
    top: 110px;
  }
}
@media (min-width: 1200px) {
  .page-header__menu {
    display: flex;
    align-items: center;
    gap: 79px;
    flex-wrap: wrap;
    position: static;
    border: none;
    background-color: transparent;
  }
}
.page-header__menu-link {
  display: block;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid #000000;
  padding-top: 15px;
  padding-bottom: 15px;
  text-decoration: none;
  text-align: center;
  color: inherit;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
}
@media (min-width: 1200px) {
  .page-header__menu-link {
    border: none;
    line-height: 18px;
    font-weight: 400;
  }
}
